.contact-filter {
  display: flex;
  justify-content: center;
  margin-block-start: 1rem;
  input {
    width: 200px;
    padding: 8px;
    border-radius: 5px;
    border: none;
    outline: none;
    transition: all 0.2s;
    &:focus {
      box-shadow: inset 0 0 0 1px #0079bf;
    }
  }
}
